import React from "react";

function PageTitle() {
  return (
    <div>
      <div className="main-wrapper ">
        <section className="page-title bg-2">
          <div className="container">
            <div className="row">
              <div className="col-lg-12 text-center">
                <ul className="list-inline mb-0">
                  <li className="list-inline-item">
                    <a
                      href="/"
                      className="text-sm letter-spacing text-white text-uppercase font-weight-bold"
                    >
                      Home
                    </a>
                  </li>
                  <li className="list-inline-item">
                    <span className="text-white">|</span>
                  </li>
                  <li className="list-inline-item">
                    <a
                      href="#"
                      className="text-color text-uppercase text-sm letter-spacing"
                    >
                      About us
                    </a>
                  </li>
                </ul>
                <h1 className="text-lg text-white mt-2">What we are</h1>
              </div>
            </div>
          </div>
        </section>
      </div>
    </div>
  );
}

export default PageTitle;
