import { CartProvider, useCart } from 'react-use-cart';
import React from 'react'
import Navbar from '../Navbar'
import Footer from '../Home/Footer';
import ScrollToTop from 'react-scroll-to-top';

function Cart() {
  const { isEmpty, totalUniqueItems, items, updateItemQuantity, removeItem ,cartTotal } =
    useCart();

  if (isEmpty) {
    return (
      <div>
        <h4 className="text-center">Your cart is empty</h4>;
      </div>
    );
  }
  return (
    <div>
      <main class="page">
        <section class="shopping-cart dark">
          <div class="container">
            <div class="block-heading">
              <h2>Shopping Cart ({totalUniqueItems})</h2>
            </div>
            <div class="content">
              <div class="row">
                <div class="col-md-12 col-lg-8">
                  <div class="items">
                    {items.map((element) => (
                      <div class="product">
                        <div class="row">
                          <div class="col-md-3">
                            <img
                              class="img-fluid mx-auto d-block image"
                              src={element.images[0]}
                            />
                          </div>
                          <div class="col-md-8">
                            <div class="info">
                              <div class="row">
                                <div class="col-md-5 product-name">
                                  <div class="product-name">
                                    <h4 className="text-primary">
                                      {element.title}
                                    </h4>
                                    <div class="product-info">
                                      <div>
                                        Discount:{" "}
                                        <span class="value">
                                          {element.discountPercentage}$
                                        </span>
                                      </div>
                                      <div>
                                        Category:{" "}
                                        <span class="value">
                                          {element.category}
                                        </span>
                                      </div>
                                      <div>
                                        Price:{" "}
                                        <span class="value">
                                          {element.price}$
                                        </span>
                                      </div>
                                    </div>
                                  </div>
                                </div>
                                <div class="col-md-4 quantity">
                                  <label for="quantity">Quantity:</label>
                                  <br />
                                  <span>{element.quantity}</span>
                                  <br />
                                  <button
                                    onClick={() =>
                                      updateItemQuantity(
                                        element.id,
                                        element.quantity - 1
                                      )
                                    }
                                    className="oma"
                                  >
                                    -
                                  </button>
                                  <button
                                    onClick={() =>
                                      updateItemQuantity(
                                        element.id,
                                        element.quantity + 1
                                      )
                                    }
                                    className="omaa mx-2"
                                  >
                                    +
                                  </button>
                                  <button
                                    onClick={() => removeItem(element.id)}
                                    className="omar "
                                  >
                                    &times;
                                  </button>
                                </div>
                                <div class="col-md-3 price">
                                  <br />
                                  <span>
                                    ${element.price * element.quantity}
                                  </span>
                                </div>
                              </div>
                            </div>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
                <div class="col-md-12 col-lg-4">
                  <div class="summary">
                    <h3>Summary</h3>
                    <div class="summary-item">
                      <span class="text">Subtotal</span>
                      <span class="price">{cartTotal}$</span>
                    </div>
                    <div class="summary-item">
                      <span class="text">Discount</span>
                      <span class="price">10$</span>
                    </div>
                    <div class="summary-item">
                      <span class="text">Shipping</span>
                      <span class="price">30$</span>
                    </div>
                    <div class="summary-item">
                      <span class="text">Total</span>
                      <span class="price">{Math.round(cartTotal+20)}$</span>
                    </div>
                    <button
                      type="button"
                      class="btn btn-primary btn-lg btn-block"
                    >
                      Checkout
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </main>
    </div>
  );
}

function AllCart() {
  return (
    <CartProvider>
        <Navbar/>
        <div>
          <img src="/images/shopping-cart-dark-background_781958-2447.jpg" className='img-fluid w-100 h-25'/>
        </div>
        <br/>
        <br/>
        <Cart/>
        <br/>
        <Footer/>
        <ScrollToTop
        smooth
        color="#000"
        style={{ backgroundColor: "yellow", width: "50px" }}
        viewBox="0 0 512 512"
        svgPath="M233.4 105.4c12.5-12.5 32.8-12.5 45.3 0l192 192c12.5 12.5 12.5 32.8 0 45.3s-32.8 12.5-45.3 0L256 173.3 86.6 342.6c-12.5 12.5-32.8 12.5-45.3 0s-12.5-32.8 0-45.3l192-192z"
      ></ScrollToTop>
    </CartProvider>
  )
}

export default AllCart