import React from "react";
import { NavLink } from "react-router-dom";

function Slider() {
  return (
    <div>
      <div className="main-wrapper ">
        <section className="slider">
          <div className="container">
            <div className="row">
              <div className="col-md-8">
                <span className="h6 d-inline-block mb-4 subhead text-uppercase">
                  Serious Training
                </span>
                <h1 className="text-uppercase text-white mb-5">
                  Step up your <span className="text-color">fitness Challange</span>
                  <br/>
                  with us
                </h1>

                <NavLink to="https://www.goldsgym.com/join-golds-gym-now/" target="_blank" className="btn btn-main ">
                  Join Us <i className="ti-angle-right ml-3"></i>
                </NavLink>
              </div>
            </div>
          </div>
        </section>
      </div>
    </div>
  );
}

export default Slider;
