import React from "react";
import Navbar from "../Navbar";
import Tittle from "./Tittle";
import SectionCourse from "./SectionCourse";
import Footer from "../Home/Footer";
import ScrollToTop from "react-scroll-to-top";
import { CartProvider } from "react-use-cart";

function AllCourses() {
  return (
    <CartProvider>
      <Navbar />
      <div className="main-wrapper">
        <Tittle />
        <SectionCourse />
        <Footer />
        <ScrollToTop
          smooth
          color="#000"
          style={{ backgroundColor: "yellow", width: "50px" }}
          viewBox="0 0 512 512"
          svgPath="M233.4 105.4c12.5-12.5 32.8-12.5 45.3 0l192 192c12.5 12.5 12.5 32.8 0 45.3s-32.8 12.5-45.3 0L256 173.3 86.6 342.6c-12.5 12.5-32.8 12.5-45.3 0s-12.5-32.8 0-45.3l192-192z"
        ></ScrollToTop>
      </div>
    </CartProvider>
  );
}

export default AllCourses;
