import React from "react";
import { NavLink } from "react-router-dom";
import { CartProvider, useCart } from "react-use-cart";

function Navbar() {
  const{totalItems}=useCart();
  return (
    <CartProvider>
      <nav className="navbar navbar-expand-lg navigation fixed-top" id="navbar">
        <div className="container-fluid">
          <NavLink to="https://www.goldsgym.com/?srsltid=AfmBOooKmKjkMj63K4bohI1y_UacpxGjjgg5Kj5DLMDmACM3ixiTVSZF">
            <img src="/images/fullcolor.svg" className="img-fluid gold" />
          </NavLink>
          <NavLink className="navbar-brand" to="/">
            <h2 className="text-white text-capitalize">
              Gold's<span className="text-color"> Gym </span>
            </h2>
          </NavLink>
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarsid"
            aria-controls="navbarsid"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="ti-view-list"></span>
          </button>
          <div className="collapse text-center navbar-collapse" id="navbarsid">
            <ul className="navbar-nav mx-auto">
              <li className="nav-item active">
                <NavLink className="nav-link" to="/">
                  Home <span className="sr-only">(current)</span>
                </NavLink>
              </li>

              <li className="nav-item dropdown">
                <a
                  className="nav-link dropdown-toggle"
                  href="#"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  Pages.
                </a>
                <ul className="dropdown-menu">
                  <li>
                    <NavLink className="dropdown-item" to="/About">
                      About
                    </NavLink>
                  </li>
                  <li>
                    <NavLink className="dropdown-item" to="/Trainer">
                      Trainer
                    </NavLink>
                  </li>
                  <li>
                    <NavLink className="dropdown-item" to="/Courses">
                      Courses
                    </NavLink>
                  </li>
                </ul>
              </li>

              <li className="nav-item">
                <NavLink className="nav-link" to="/Service">
                  Services
                </NavLink>
              </li>

              <li className="nav-item">
                <NavLink className="nav-link" to="/MemeberShip">
                  Memebership
                </NavLink>
              </li>

              <li className="nav-item dropdown">
                <a
                  className="nav-link dropdown-toggle"
                  href="#"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="false"
                >
                  Blog.
                </a>
                <ul className="dropdown-menu">
                  <li>
                    <NavLink className="dropdown-item" to="/BlogGrid">
                      Blog Grid
                    </NavLink>
                  </li>
                  <li>
                    <NavLink className="dropdown-item" to="/BlogSidebar">
                      Blog Sidebar
                    </NavLink>
                  </li>
                  <li>
                    <NavLink className="dropdown-item" to="/BlogDetails">
                      Blog Details
                    </NavLink>
                  </li>
                </ul>
              </li>

              <li className="nav-item">
                <NavLink className="nav-link" to="/Contact">
                  Contact
                </NavLink>
              </li>

              <li className="nav-item">
                <NavLink className="nav-link" to="/Productes">
                  Productes
                </NavLink>
              </li>
            </ul>
            <button class="til">
              <NavLink to="/Cart">
              <div class="default-btn">
                <svg class="css-i6dzq1" stroke-linejoin="round" stroke-linecap="round" fill="none" stroke-width="2" stroke="#FFF" height="20" width="20" viewBox="0 0 24 24"><path d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"></path><circle r="3" cy="12" cx="12"></circle></svg>
                <span>Shop Now</span>
              </div>
                <div class="hover-btn">
                  <svg class="css-i6dzq1" stroke-linejoin="round" stroke-linecap="round" fill="none" stroke-width="2" stroke="#ffd300" height="20" width="20" viewBox="0 0 24 24"><circle r="1" cy="21" cx="9"></circle><circle r="1" cy="21" cx="20"></circle><path d="M1 1h4l2.68 13.39a2 2 0 0 0 2 1.61h9.72a2 2 0 0 0 2-1.61L23 6H6"></path></svg>
                  <span> Cart ({totalItems}) </span>
                </div>  
              </NavLink>
            </button> 
          </div>
        </div>
      </nav>
    </CartProvider>
  );
}

export default Navbar;



