import * as React from "react";
import { createRoot } from "react-dom/client";
import {createBrowserRouter, RouterProvider,} 
from "react-router-dom";
import App from "./App";
import AllAbout from "./About/AllAbout";
import AllTrainer from "./Trainner/AllTrainer";
import AllCourses from "./Courses/AllCourses";
import AllService from "./Service/AllService";
import AllMemeber from "./MemeberShip/AllMemeber";
import AllBlogGrid from "./BlogGrid/AllBlogGrid";
import AllBlogDetails from "./BlogDetails/AllBlogDetails";
import AllContact from "./Contact/AllContact";
import AllLocation from "./OurLocation/AllLocation";
import AllProductes from "./Productes/AllProductes";
import AllCart from "./Cart/AllCart";


const router = createBrowserRouter([
  {
    path: "/",
    element: <App/>,
  },
  {
    path: "About",
    element: <AllAbout/>,
  },
  {
    path: "Trainer",
    element: <AllTrainer/>,
  },
  {
    path: "Courses",
    element: <AllCourses/>,
  },
  {
    path: "Service",
    element: <AllService/>,
  },
  {
    path: "MemeberShip",
    element: <AllMemeber/>,
  },
  {
    path: "BlogGrid",
    element: <AllBlogGrid/>,
  },
  {
    path: "BlogSidebar",
    element: <AllBlogGrid/>,
  },
  {
    path: "BlogDetails",
    element: <AllBlogDetails/>,
  },
  {
    path: "Contact",
    element: <AllContact/>,
  },
  {
    path: "OurLocation",
    element: <AllLocation/>,
  },
  {
    path: "Productes",
    element: <AllProductes/>,
  },
  {
    path: "Cart",
    element: <AllCart/>,
  },

]);

createRoot(document.getElementById("root")).render(
  <RouterProvider router={router} />);